import React, { useState } from 'react';
import { View, StyleSheet } from 'react-native';
import { PRIMARY, WHITE } from '../../styles/COLORS';
import { appImages } from '../../utils/appImages';
import PrimaryInput from '../../components/molecules/PrimaryInput';
import PrimaryButton from '../../components/atoms/PrimaryButton';
import Image from 'react-native-scalable-image';
import { useNavigation } from '@react-navigation/native';
import { authStackScreens } from '../../utils/appScreens';

type UserFormProps = {};

const UserForm: React.FC<UserFormProps> = () => {
  const navigation = useNavigation();
  const [name, setName] = useState<string>('');
  const [email, setEmail] = useState<string>('');
  const [isLoading, setisLoading] = useState<boolean>(false)
  const handleRegister = () => {
    setisLoading(true)


    // Handle user registration logic here

    setTimeout(() => {
      setisLoading(false)
      navigation.navigate(authStackScreens.UserProfile.name)

    }, 2000);

  };

  return (
    <View style={styles.container}>
      <View style={styles.logoContainer}>
        <Image width={120} source={{ uri: appImages.logo }} style={styles.logo} />
      </View>

      <View style={styles.formContainer}>
        <View style={styles.inputContainer}>
          <PrimaryInput
            required
            placeholder="Name"
            value={name}
            onChangeText={setName}
          />
        </View>
        <View style={styles.inputContainer}>
          <PrimaryInput
            placeholder="Email"
            value={email}
            onChangeText={setEmail}
          />
        </View>

        <View style={styles.buttonContainer}>
          <PrimaryButton

            loading={isLoading}
            content="Register" onPress={handleRegister} />
        </View>
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flexDirection: 'column',
    flex: 1,
    backgroundColor: PRIMARY,
  },
  logoContainer: {
    flex: 1,
    backgroundColor: PRIMARY,
    justifyContent: 'center',
    alignItems: 'center',
  },
  logo: {
    height: 130,
  },
  formContainer: {
    flex: 5,
    borderTopEndRadius: 30,
    borderTopStartRadius: 30,
    backgroundColor: WHITE,
    paddingHorizontal: 20,
    paddingVertical: 5,
  },
  inputContainer: {
    marginVertical: 5,
  },
  buttonContainer: {
    marginVertical: 10,
  },
});

export default UserForm;
