import React from 'react';
import { View, Text, TouchableOpacity, StyleSheet, StyleProp, ViewStyle, TextStyle, ActivityIndicator } from 'react-native';
import { PRIMARY, WHITE } from '../../styles/COLORS';

interface Props {
    outline?: boolean;
    content: string;
    onPress: () => void;
    containerStyle?: StyleProp<ViewStyle>;
    textStyle?: StyleProp<TextStyle>;
    loading?: boolean;
}

export default function PrimaryButton({ outline, content, onPress, containerStyle, textStyle, loading }: Props) {
    return (
        <TouchableOpacity onPress={onPress} disabled={loading}>
            <View style={[styles.container, containerStyle, outline && styles.outlineContainer, loading && styles.disabledContainer]}>
                {loading ? <ActivityIndicator size="small" color={outline ? PRIMARY : WHITE} /> : (
                    <Text style={[styles.text, textStyle, outline && styles.outlineText]}>
                        {content}
                    </Text>
                )}
            </View>
        </TouchableOpacity>
    );
}

const styles = StyleSheet.create({
    container: {
        paddingVertical: 10,
        backgroundColor: PRIMARY,
        borderRadius: 5,
        justifyContent: 'center',
        alignItems: 'center',
    },
    outlineContainer: {
        backgroundColor: WHITE,
        borderWidth: 1,
        borderColor: PRIMARY,
    },
    text: {
        color: WHITE,
        fontWeight: 'bold',
        fontSize: 16,
        textAlign: 'center',
    },
    outlineText: {
        color: PRIMARY,
    },
    disabledContainer: {
        opacity: 0.5,
    },
});
