const OTPInput = () => {
  const navigation = useNavigation();
  const [isLoading, setisLoading] = useState<boolean>(false)
  const handleOtpInput = () => {
    console.log('OTP Input')
    setisLoading(true)
    setTimeout(() => {
      setisLoading(false)
      navigation.navigate(authStackScreens.UserForm.name)
    }, 2000);

  }

  return (
    <View style={styles.container}>

      <ScreenHeader />
      <View style={styles.titleContainer}>
        <Text style={styles.title}>Enter 6 digit OTP sent to</Text>

        <Text style={{
          fontSize: 16,
          marginTop: 20,
        }}>+91 9926579383</Text>
      </View>

      <View style={styles.inputContainer}>
        <SplitOtpInput
          numInputs={6}
          onChangeText={(text) => { console.log(text) }}

        />
      </View>

      <View style={styles.buttonContainer}>
        <PrimaryButton
          loading={isLoading}
          onPress={handleOtpInput}
          content='Verify'
        />
      </View>
    </View>
  )
}

