import { createBottomTabNavigator } from "@react-navigation/bottom-tabs";
import HomeScreen from "../scenes/BottomTabs/HomeScreen";
import { bottomTabScreens } from "../utils/appScreens";
import WishlistScreen from "../scenes/BottomTabs/WishlistScreen";
import AccountScreen from "../scenes/BottomTabs/AccountScreen";
import CategoryScreen from "../scenes/BottomTabs/CategoryScreen";
import SettingsScreen from "../scenes/BottomTabs/SettingsScreen";

import BottomTabIcon from "../components/atoms/BottomTabIcon";
export default function BottomTabNavigator() {
  const BottomTab = createBottomTabNavigator();

  return (
    <BottomTab.Navigator
      screenOptions={{
        headerShown: false,
        tabBarShowLabel: false,
      }}
      initialRouteName={bottomTabScreens.HomeScreen.name}
    >
      <BottomTab.Screen
        options={{
          tabBarIcon: ({ focused }) => (
            <BottomTabIcon IconName={focused ? "heart" : "heart-outline"} />
          ),
        }}
        name={bottomTabScreens.WishlistScreen.name}
        component={WishlistScreen}
      />
      <BottomTab.Screen
        options={{
          tabBarIcon: ({ focused }) => (
            <BottomTabIcon IconName={focused ? "grid" : "grid-outline"} />
          ),
        }}
        name={bottomTabScreens.CategoryScreen.name}
        component={CategoryScreen}
      />
      <BottomTab.Screen
        options={{
          tabBarIcon: ({ focused }) => (
            <BottomTabIcon IconName={focused ? "rocket" : "rocket-outline"} />
          ),
        }}
        name={bottomTabScreens.HomeScreen.name}
        component={HomeScreen}
      />
      <BottomTab.Screen
        options={{
          tabBarIcon: ({ focused }) => (
            <BottomTabIcon IconName={focused ? "person" : "person-outline"} />
          ),
        }}
        name={bottomTabScreens.AccountScreen.name}
        component={AccountScreen}
      />
      <BottomTab.Screen
        options={{
          tabBarIcon: ({ focused }) => (
            <BottomTabIcon
              IconName={focused ? "settings" : "settings-outline"}
            />
          ),
        }}
        name={bottomTabScreens.SettingsScreen.name}
        component={SettingsScreen}
      />
    </BottomTab.Navigator>
  );
}
