import { View, Text, StyleSheet } from 'react-native';
import React, { useEffect, useState } from 'react';
import Image from 'react-native-scalable-image';
import { GRAYSCALE, WHITE } from '../../styles/COLORS';
import { Ionicons } from '@expo/vector-icons'
import { TouchableOpacity, TouchableWithoutFeedback } from 'react-native-gesture-handler';
import { useNavigation } from '@react-navigation/native';
import { appScreens } from '../../utils/appScreens';
import AsyncStorage from '@react-native-async-storage/async-storage';
interface ProductCardProps {
    imageUrl: string;
    productName: string;
    ourPrice: number;
    mrp: number;
    productId: any;
}
const ProductCard: React.FC<ProductCardProps> = ({
    imageUrl,
    productName,
    ourPrice,
    mrp,
    productId
}) => {
    const [InWishlist, setInWishlist] = useState(false)
    const navigation = useNavigation()
    useEffect(() => {
        const checkWishlist = async () => {
            const wishlistString = await AsyncStorage.getItem('wishlist');
            if (wishlistString) {
                const wishlist = JSON.parse(wishlistString);
                setInWishlist(wishlist.some((item: any) => item.productName === productName));
            }
        };

        checkWishlist();
    }, [InWishlist]);

    const handleWishlistPress = async () => {
        if (true) { // replace with your condition for user login
            const wishlistString = await AsyncStorage.getItem('wishlist');
            let wishlist = wishlistString ? JSON.parse(wishlistString) : [];

            if (InWishlist) {
                wishlist = wishlist.filter((item: any) => item.productName !== productName);
                await AsyncStorage.setItem('wishlist', JSON.stringify(wishlist));
                setInWishlist(false);
            } else {
                wishlist.push({ productName });
                await AsyncStorage.setItem('wishlist', JSON.stringify(wishlist));
                setInWishlist(true);
            }
        } else {
            alert('Please login to add to wishlist');
        }
    };
    return (
        <View style={styles.container}>
            <TouchableWithoutFeedback

                onPress={() => {
                    navigation.navigate(appScreens.ProductDetails.name, {
                        productId
                    })
                }}
            >

                <Image
                    width={130}

                    borderTopLeftRadius={10}
                    borderTopRightRadius={10}
                    source={{ uri: imageUrl }}
                />
            </TouchableWithoutFeedback>

            <View style={styles.titleContainer}>
                <Text numberOfLines={2} style={styles.titleText}>
                    {productName}
                </Text>
            </View>
            <View style={styles.priceContainer}>
                <View style={styles.priceDetailsContainer}>
                    <Text style={styles.priceText}>
                        ₹ {ourPrice}
                    </Text>
                    <Text style={styles.mrpText}>
                        ₹ {mrp}
                    </Text>
                </View>
                <TouchableOpacity

                    onPress={handleWishlistPress}
                    style={styles.iconContainer}>
                    <Ionicons name={InWishlist ? "heart" : "heart-outline"} size={20} color={GRAYSCALE.GRAY_800} />
                </TouchableOpacity>
            </View>
        </View>
    );
};
const styles = StyleSheet.create({
    container: {
        elevation: 5,
        shadowColor: 'black',
        shadowOffset: { width: 2, height: 4 },
        shadowOpacity: 0.2,
        shadowRadius: 3,

        borderColor: '#efefef',
        backgroundColor: WHITE,
        borderRadius: 10,
        width: 130,
    },
    titleContainer: {
        paddingVertical: 3,
        marginHorizontal: 5,
        borderBottomWidth: 0.4,
    },
    titleText: {
        fontWeight: 'bold',
        fontSize: 13,
        marginHorizontal: 3,
        paddingVertical: 5,
        height: 40,
        width: 100,
    },
    priceContainer: {
        flexDirection: 'row',
        margin: 5,
    },
    priceDetailsContainer: {
        flex: 1,
    },
    priceText: {
        fontWeight: 'bold',
        fontSize: 12
    },
    mrpText: {
        fontWeight: 'bold',
        color: GRAYSCALE.GRAY_600,
        fontSize: 10,
        textDecorationLine: 'line-through',
    },
    iconContainer: {
        flex: 1,
        justifyContent: 'center',
        alignItems: 'flex-end',
        paddingRight: 10,
    },
});
export default ProductCard;
