import React, { useState, useRef } from 'react';
import { View, TextInput, StyleSheet } from 'react-native';

interface Props {
  numInputs: number;
  inputContainerStyle?: any;
  inputStyle?: any;
  onChangeText: (code: string) => void;
}

export default function SplitOtpInput({ numInputs, inputContainerStyle, inputStyle, onChangeText }: Props) {
  const [code, setCode] = useState<string[]>(new Array(numInputs).fill(''));

  const inputs = useRef<TextInput[]>([]);

  const handleCodeChange = (value: string, index: number) => {
    const newCode = [...code];
    if (value === '') {
      newCode[index] = '';
    } else {
      newCode[index] = value;
      if (index < numInputs - 1) {
        inputs.current[index + 1].focus();
      }
    }
    setCode(newCode);
    onChangeText(newCode.join(''));
  };

  const handlePaste = (event: any) => {
    const pastedText = event.nativeEvent.clipboardData.getData('text');
    const pastedCode = pastedText.split('').slice(0, numInputs);
    const newCode = [...code];
    pastedCode.forEach((char, i) => {
      newCode[i] = char;
    });
    setCode(newCode);
    onChangeText(newCode.join(''));
  };

  const handleKeyPress = (event: any, index: number) => {
    if (event.nativeEvent.key === 'Backspace' && index > 0) {
      inputs.current[index - 1].focus();
    }
  };

  const renderInputs = () => {
    const inputsArray = [];
    for (let i = 0; i < numInputs; i++) {
      inputsArray.push(
        <View key={i} style={[styles.inputContainer, inputContainerStyle]}>
          <TextInput
            style={[styles.input, inputStyle]}
            value={code[i]}
            maxLength={1}
            keyboardType='numeric'
            onChangeText={(value) => handleCodeChange(value, i)}
            onKeyPress={(event) => handleKeyPress(event, i)}
            onPaste={(event) => handlePaste(event)}
            ref={(ref) => (inputs.current[i] = ref)}
          />
        </View>
      );
    }
    return inputsArray;
  };

  return (
    <View style={styles.container}>
      {renderInputs()}
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginVertical: 10,
  },
  inputContainer: {
    marginHorizontal: 5,
    justifyContent: 'center',
    alignItems: 'center',
    borderWidth: 1,
    borderRadius: 5,
    borderColor: '#cccccc',
    width: 50,
    height: 50,
  },
  input: {
    fontSize: 24,
    textAlign: 'center',
  },
});
