import React, { useRef, useState } from 'react';
import { View, Text, TextInput, TouchableOpacity, StyleSheet } from 'react-native';

import { APP_CONSTANTS } from '../../utils/appConstants';
import { PRIMARY } from '../../styles/COLORS';
import ScreenHeader from '../../components/molecules/ScreenHeader';
import PrimaryButton from '../../components/atoms/PrimaryButton';
import { useNavigation } from '@react-navigation/native';
import { authStackScreens } from '../../utils/appScreens';


const PhoneInput = () => {
  const [PhoneInput, setPhoneInput] = useState('')
  const [isLoading, setisLoading] = useState<boolean>(false)
  const navigation = useNavigation();
  const handlePhoneInput = () => {
    setisLoading(true)
    setTimeout(() => {
      setisLoading(false)
      navigation.navigate(authStackScreens.OtpInputScreen.name)
    }, 2000);
  }
  return (
    <View style={styles.container}>

      <ScreenHeader />
      <View style={styles.titleContainer}>
        <Text style={styles.title}>Sign-In into</Text>
        <Text style={styles.title}>{APP_CONSTANTS.APP_NAME}</Text>
        <Text style={{
          fontSize: 16,
          marginTop: 20,
        }}>A 6 digit OTP will be sent on your Phone Number !</Text>
      </View>

      <View style={styles.inputContainer}>
        <TextInput
          keyboardType='phone-pad'
          maxLength={10}
          style={styles.textInput}
          placeholder='Enter Your Phone Number'
        />
      </View>

      <View style={styles.buttonContainer}>
        <PrimaryButton
          loading={isLoading}
          onPress={handlePhoneInput}
          content='Sign-In'
        />
      </View>
    </View>
  )
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    flexDirection: 'column',
  },
  titleContainer: {
    flex: 1,
    justifyContent: 'center',
    marginTop: 40,
    marginHorizontal: 20,
  },
  title: {
    fontSize: 30,
    fontWeight: 'bold',
    color: PRIMARY,
  },
  inputContainer: {
    flex: 1,
    justifyContent: 'center',
    marginHorizontal: 20,
    marginTop: 40,
  },
  textInput: {
    paddingVertical: 10,
    paddingHorizontal: 20,
    borderBottomWidth: 1,
    borderBottomColor: PRIMARY,
  },
  buttonContainer: {
    flex: 1,
    justifyContent: 'flex-end',
    marginHorizontal: 20,
    marginBottom: 20,
  },
  button: {
    paddingVertical: 15,
    paddingHorizontal: 40,
    borderRadius: 10,
    backgroundColor: PRIMARY,
  },
  buttonText: {
    color: 'white',
    fontSize: 18,
    fontWeight: 'bold',
    textAlign: 'center',
  },
});

export default PhoneInput;
