import { useFocusEffect, useNavigation } from '@react-navigation/native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { authStackScreens } from '../utils/appScreens';
import React from 'react';
import PhoneInput from '../scenes/Auth/PhoneInput';
import OTPInput from '../scenes/Auth/OTPInput';
import UserForm from '../scenes/Auth/UserForm';
import UserProfile from '../scenes/Auth/UserProfile';
import { CardStyleInterpolators, createStackNavigator } from '@react-navigation/stack';

function authNavigator() {
    const navigation = useNavigation();
    const Stack = createStackNavigator();

    useFocusEffect(
        React.useCallback(() => {
            const checkToken = async () => {
                const token = await AsyncStorage.getItem('token');
                if (token) {
                    navigation.replace(authStackScreens.UserProfile.name);
                } else {
                    navigation.replace(authStackScreens.PhoneInputScreen.name);
                }
            };
            checkToken();
        }, [])
    );

    return (
        <Stack.Navigator screenOptions={{ headerShown: false, cardStyleInterpolator: CardStyleInterpolators.forHorizontalIOS }}>
            <Stack.Screen options={{
                animationEnabled: false,
            }} name={authStackScreens.PhoneInputScreen.name} component={PhoneInput} />
            <Stack.Screen name={authStackScreens.OtpInputScreen.name} component={OTPInput} />
            <Stack.Screen name={authStackScreens.UserForm.name} component={UserForm} />
            <Stack.Screen options={{
                animationEnabled: false,
            }} name={authStackScreens.UserProfile.name} component={UserProfile} />
        </Stack.Navigator>
    );
}

export default authNavigator;