import React, { useState } from 'react';
import { View, Text, TextInput, StyleSheet, TextInputProps } from 'react-native';
import { GRAYSCALE, PRIMARY, WARNING } from '../../styles/COLORS';

interface PrimaryInputProps extends TextInputProps {
    placeholder: string;
    required?: boolean;
}

const PrimaryInput: React.FC<PrimaryInputProps> = ({ placeholder, required = false, ...rest }) => {
    const [isActive, setIsActive] = useState(false);
    const [isTouched, setIsTouched] = useState(false);
    const [text, setText] = useState('');

    const handleFocus = () => {
        setIsActive(true);
        setIsTouched(true);
    };

    const handleBlur = () => {
        setIsActive(false);
    };

    const handleTextChange = (text: string) => {
        setText(text);
    };

    const showWarning = isTouched && required && !text;

    return (
        <View style={styles.container}>
            <TextInput
                placeholder={`${placeholder} ${required ? '(Required)' : '(Optional)'} `}
                style={[
                    styles.input,
                    isActive && { borderColor: PRIMARY },
                    showWarning && { borderColor: WARNING },
                ]}
                onFocus={handleFocus}
                onBlur={handleBlur}
                onChangeText={handleTextChange}
                {...rest}
            />
            {showWarning && <Text style={styles.warning}>This field is required</Text>}
        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        marginVertical: 10,
    },
    input: {
        borderWidth: 1,
        borderColor: GRAYSCALE.GRAY_600,
        fontSize: 16,
        paddingVertical: 10,
        paddingHorizontal: 5,
        borderRadius: 5,
    },
    warning: {
        color: WARNING,
        fontSize: 14,
        marginTop: 5,
    },
});

export default PrimaryInput;
