import React from 'react';
import { StyleSheet, View, FlatList, useWindowDimensions, Vibration } from 'react-native';
import CategoryCard from '../../components/molecules/CategoryCard';
import ScreenHeader from '../../components/molecules/ScreenHeader';

const categories = [
  { id: '1', name: 'Clothing', imageUrl: 'https://source.unsplash.com/random/400x400?clothing' },
  { id: '2', name: 'Shoes', imageUrl: 'https://source.unsplash.com/random/400x400?shoes' },
  { id: '3', name: 'Accessories', imageUrl: 'https://source.unsplash.com/random/400x400?accessories' },
  { id: '4', name: 'Beauty', imageUrl: 'https://source.unsplash.com/random/400x400?beauty' },
  { id: '5', name: 'Electronics', imageUrl: 'https://source.unsplash.com/random/400x400?electronics' },
  { id: '6', name: 'Food', imageUrl: 'https://source.unsplash.com/random/400x400?food' },
];

const CategoryScreen = () => {
  const { width } = useWindowDimensions();

  const numColumns = Math.floor(width / 100); // assuming each card is 100px wide

  const renderCategory = ({ item }) => (
    <View style={styles.categoryContainer}>
      <CategoryCard
        imageUrl={item.imageUrl}
        categoryName={item.name}
        onPress={() => {
          Vibration.vibrate();
          // handle category press
        }}
      />
    </View>
  );

  return (
    <View style={styles.container}>
      <ScreenHeader />
      <FlatList
        data={categories}
        renderItem={renderCategory}
        keyExtractor={(item) => item.id}
        horizontal={false}
        numColumns={numColumns}
        contentContainerStyle={styles.flatListContentContainer}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
  },
  categoryContainer: {
    flex: 1,
    marginVertical: 10,
    marginHorizontal: 10,
  },
  flatListContentContainer: {
    paddingBottom: 20,
  },
});

export default CategoryScreen;
