
import { FlatList, Text, View } from 'react-native'
import React, { Component, createRef } from 'react'
import * as Icons from '@expo/vector-icons';
import Image from 'react-native-scalable-image';
import { APP_CONSTANTS } from '../../utils/appConstants';
interface IProductImage {
    id: number;
    image: string;
}

interface IProductImageSliderState {
    activeIndex: number;
    images: IProductImage[];
}

export default class ProducImageSlider extends Component {


    flatListRef = createRef<FlatList<IProductImage>>();

    constructor(props) {
        super(props);
        this.state = {

            activeIndex: 1,
            images: [
                {
                    id: 1,
                    image:
                        'https://i.ibb.co/Rj739zQ/331559767-986775262295442-3491856230846526797-n.jpg',
                },
                {
                    id: 2,
                    image:
                        'https://i.ibb.co/sWHzsTp/331324892-2183919618444880-3217630208281975406-n.jpg',
                },
                {
                    id: 3,
                    image:
                        'https://i.ibb.co/0YYgLpp/331542829-1356348411822391-1952014847819709226-n.jpg',
                },
                {
                    id: 4,
                    image:
                        'https://i.ibb.co/wdym6NT/331317423-511828354446468-5204158995911790086-n.jpg',
                },
                {
                    id: 5,
                    image:
                        'https://i.ibb.co/Sy2txHN/331685568-1151844535476107-7407933853412089538-n.jpg',
                },
            ],

        };

    }

    _handleNext = () => {
        const { images, activeIndex } = this.state;
        const newIndex = activeIndex === images.length ? 1 : activeIndex + 1;
        this.flatListRef.current?.scrollToIndex({ index: newIndex - 1 });
    };

    _handlePrev = () => {
        const { images, activeIndex } = this.state;
        const newIndex = activeIndex === 1 ? images.length : activeIndex - 1;
        this.flatListRef.current?.scrollToIndex({ index: newIndex - 1 });
    };

    _onViewableItemsChanged = ({ viewableItems, changed }) => {
        const activeIndex = viewableItems[0].item
            ? viewableItems[0].item.id
            : 1;

        // handle next and prev on change

        this.setState({ activeIndex }, () => {
            //  console.log("Active Index", this.state.activeIndex);
        });
    };

    renderDot = (index) => (
        <View style={{
            marginHorizontal: 2,
            marginVertical: 10,
        }}>
            <Text>
                <Icons.FontAwesome
                    name={this.state.activeIndex == index ? 'circle' : "circle-o"}
                    size={12}

                />
            </Text>
        </View>
    );
    render() {
        const { images } = this.state;

        return (
            <View>
                <FlatList
                   
                    ref={this.flatListRef}
                    viewabilityConfig={{
                        itemVisiblePercentThreshold: 40,
                    }}
                    onViewableItemsChanged={this._onViewableItemsChanged}
                    horizontal
                    pagingEnabled
                    showsHorizontalScrollIndicator={false}
                    data={images}
                    renderItem={({ item }) => (
                        <View
                            style={{
                                width: APP_CONSTANTS.WIDTH,
                                alignItems: 'center',
                            }}>
                            <Image
                                source={{ uri: item.image }}
                                width={APP_CONSTANTS.WIDTH - 40}
                                borderRadius={10}
                            />
                        </View>
                    )}
                />
                <View style={{ flexDirection: 'row', justifyContent: 'center' }}>
                    {images.map((item, index) => this.renderDot(item.id))}
                </View>
            </View>

        )
    }
}