import React from 'react';
import { View, Text, StyleSheet } from 'react-native';

import * as Icons from '@expo/vector-icons';
import { PRIMARY } from '../../../styles/COLORS';

const ProductTitle = () => {
  const renderStars = (rating) => {
    const stars = [];
    for (let i = 0; i < 5; i++) {
      if (i < rating) {
        stars.push(<Icons.FontAwesome key={i} name='star' size={15} color={PRIMARY} />);
      } else {
        stars.push(<Icons.FontAwesome key={i} name='star-o' size={15} color={PRIMARY} />);
      }
    }
    return stars;
  };

  return (
    <View style={styles.container}>
      <View style={styles.title}>
        <Text style={styles.titleText}>Money Talks</Text>
        <View style={styles.stars}>
          {renderStars(4.5)}
          <Text style={styles.reviews}>{' '}(245 Reviews)</Text>
        </View>
      </View>
      <View style={styles.price}>
        <Text style={styles.priceText}>₹ 100.00</Text>
        <Text style={styles.discountedPrice}>₹ 120.00</Text>
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    marginHorizontal: 20,
    marginVertical: 10,
    flexDirection: 'row',
  },
  title: {
    flex: 1,
  },
  titleText: {
    fontSize: 24,
    fontWeight: 'bold',
  },
  stars: {
    flexDirection: 'row',
    alignItems: 'center',
    marginVertical: 5,
  },
  reviews: {
    fontSize: 12,
    color: PRIMARY,
  },
  price: {
    justifyContent: 'flex-end',
    alignItems: 'flex-end',
  },
  priceText: {
    fontSize: 24,
    fontWeight: 'bold',
    color: PRIMARY,
  },
  discountedPrice: {
    fontSize: 12,
    color: PRIMARY,
    textDecorationLine: 'line-through',
  },
});

export default ProductTitle;
