import { View, Text, StyleSheet } from 'react-native';
import React, { useEffect, useState } from 'react';
import Image from 'react-native-scalable-image';
import { GRAYSCALE, WHITE } from '../../../styles/COLORS';
import { Ionicons } from '@expo/vector-icons'
import { TouchableOpacity, TouchableWithoutFeedback } from 'react-native-gesture-handler';
import { useNavigation } from '@react-navigation/native';

import AsyncStorage from '@react-native-async-storage/async-storage';
import { appScreens } from '../../../utils/appScreens';
import { APP_CONSTANTS } from '../../../utils/appConstants';
interface ProductCardProps {
    imageUrl: string;
    productName: string;
    ourPrice: number;
    mrp: number;
    productId: any;
}
const ProductExtendedCard: React.FC<ProductCardProps> = ({
    imageUrl,
    productName,
    ourPrice,
    mrp,
    productId
}) => {
    const [InWishlist, setInWishlist] = useState(false)
    const navigation = useNavigation()
    useEffect(() => {
        const checkWishlist = async () => {
            const wishlistString = await AsyncStorage.getItem('wishlist');
            if (wishlistString) {
                const wishlist = JSON.parse(wishlistString);
                setInWishlist(wishlist.some((item: any) => item.productName === productName));
            }
        };

        checkWishlist();
    }, [InWishlist]);

    const handleWishlistPress = async () => {
        if (true) { // replace with your condition for user login
            const wishlistString = await AsyncStorage.getItem('wishlist');
            let wishlist = wishlistString ? JSON.parse(wishlistString) : [];

            if (InWishlist) {
                wishlist = wishlist.filter((item: any) => item.productName !== productName);
                await AsyncStorage.setItem('wishlist', JSON.stringify(wishlist));
                setInWishlist(false);
            } else {
                wishlist.push({ productName });
                await AsyncStorage.setItem('wishlist', JSON.stringify(wishlist));
                setInWishlist(true);
            }
        } else {
            alert('Please login to add to wishlist');
        }
    };
    return (
        <TouchableWithoutFeedback

            onPress={() => {
                navigation.navigate(appScreens.ProductDetails.name, {
                    productId
                })
            }}
        >
            <View style={{
                alignItems: 'center',
            }}>
                <View style={styles.container}>
                    <View style={{
                        flex: 1,
                    }}>


                        <Image
                            width={140}

                            borderTopLeftRadius={10}
                            borderTopRightRadius={10}
                            source={{ uri: imageUrl }}
                        />


                    </View>

                    <View style={{
                        flex: 2,
                        padding: 5,

                    }}>
                        <View></View>

                    </View>

                </View>
            </View>
        </TouchableWithoutFeedback>

    );
};
