import React from 'react';
import { StyleSheet, Text, TouchableOpacity, View } from 'react-native';
import { PRIMARY } from '../../styles/COLORS';

interface HomeScreenLayoutProps {
    LeftText: string;
    RightText: string;
    onRightTextPress: () => void;
}

const HomeScreenLayout: React.FC<HomeScreenLayoutProps> = ({
    LeftText,
    RightText,
    onRightTextPress,
}) => {
    const { container, leftContainer, categoryText, rightContainer, viewAllText } =
        styles;

    return (
        <View style={container}>
            <View style={leftContainer}>
                <Text style={categoryText}>{LeftText}</Text>
            </View>
            <TouchableOpacity onPress={onRightTextPress} style={rightContainer}>
                <Text style={viewAllText}>{RightText}</Text>
            </TouchableOpacity>
        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        flexDirection: 'row',
        paddingVertical: 10,
        marginHorizontal: 10,
    },
    leftContainer: {
        flex: 1,
    },
    categoryText: {
        fontSize: 12,
        fontWeight: 'bold',
        color: PRIMARY,
    },
    rightContainer: {
        flex: 1,
        alignItems: 'flex-end',
    },
    viewAllText: {
        fontSize: 12,
        fontWeight: 'bold',
        color: PRIMARY,
    },
});

export default HomeScreenLayout;
